.Category{
}
.Category h2{
    margin-bottom: 1.5rem;
    font-weight: 300;
    font-size: 24px;
    /*
    color: #fff;
    display: inline-block;
    width: min-content;
    */
    color: #FFF;
    background-color: var(--headerColor);
    padding:2px 5px 10px;  
    letter-spacing: 0.5px;  
    position: relative;
    background-size: 150px;
    background-repeat: no-repeat;
    background-position: 50% 100%;
}


.Category h2:after{
    content: '';
    top: -6px;
    left: 0;
    position: absolute;
    background-color: var(--headerColor);
    z-index: -1;
    width: 100%;
    height: 15px;
    transform: skewY(-1deg);
}

.Category h2:before{
    content: '';
    bottom: -6px;
    left: 0;
    position: absolute;
    background-color: var(--headerColor);
    z-index: -1;
    width: 100%;
    height: 15px;
    transform: skewY(1deg);
}

.CategoryHeader{
    color: #000;
    margin-bottom: 1rem;
    padding:10px 2.5rem;
    border-radius: 20px 20px 0 0;
    background-color: #fff;
}
.CategoryPreFooter{
    color: #000;
    background-color: #fff;
    margin: 10px 0 1rem;
    font-size: 16px;
    padding: 8px 12px;
    border: 1px solid;
    text-align: left;
}
.CategoryFooter{
    color: #fff;
    background-color: var(--headerColor);
    margin: 10px 0 1rem;
    font-size: 12px;
    padding: 8px 12px;
    text-align: left;
}

.CategoryFooter span{
    display: block;
}

.PriceBtns{
    display: flex;
    margin-bottom: 1rem;
    align-items: center;
    color: #fff;
    border:1px solid;
    border-radius: 5px;
    background-color: #104547;
    padding: 8px 5px;
    justify-content: flex-end;
}

.PriceBtns p{
    margin-right:auto;
}

.PriceBtns button{
    padding: 10px 5px;
    outline: 0;
    font-size: 12px;
    transition: background 0.5s;
    border-radius: 4px;
}

.PriceBtns button:first-of-type{
    margin-right: 10px;
}

.TakeAwayBtn{
    border:1px solid #2B954A;
    /*margin: 1rem auto;*/
}
.TakeAwayBtnActive{
    background-color: #2B954A;
    border:1px solid #2B954A;
    color: #fff;
}

.ShopBtn{
    border:1px solid #931A1F;
}
.ShopBtnActive{
    background-color: #931A1F;
    border:1px solid #931A1F;
    color: #fff;

    /*margin: 1rem auto;*/
}

.Back{
    padding-bottom: 1rem;
    padding: 1rem 0 2rem;
}

.Back a{
    border: 1px solid var(--footerColor);
    padding: 0.5rem 1rem;
    background: var(--btnColor);
    color: #fff;
    font-size: 12px;
    border-radius: 10px;
    box-shadow:  1px 1px 3px 2px #a1a1a1;
}

.Back a:not(:first-of-type){
    margin-left: 1rem;
}

