.Back span{
    font-size: 14px;
    position: relative;
    top: 2px;
}


.TopNavigation{
    position: fixed;
    top: 8px;
    left: 14px;
    z-index:100; 
}

.TopNavigation div{
    background-color: var(--btnColor);
    border-radius: 5px;  
    margin-left: 0; 
    padding: 5px; 
    -webkit-box-shadow: 1px 1px 3px 2px #a1a1a1; 
    box-shadow: 1px 1px 3px 2px #a1a1a1;
}