:root{
  --bgColor: #F0E0C6;
  --footerColor : #182029;
  --borderColor: #182029;
  --headerColor: #A67A53;
  --secHeaderColor: #A67A53;
  --priceColor: #6C809A;
  --btnColor: #6E0E0A;
}


body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}


html {
  -webkit-box-sizing: border-box;
  -moz-box-sizing: border-box;
  box-sizing: border-box;
}
*, *:before, *:after {
  -webkit-box-sizing: inherit;
  -moz-box-sizing: inherit;
  box-sizing: inherit;
}

body{
  /*font-family: 'Roboto', sans-serif;*/
  /*background-color: var(--bgColor);*/
  background-color: var(--bgColor);
  background-image: url('./motivo_sokratis.png');
  background-repeat: repeat;
  background-size: 450px 500px;
  font-family: 'Manrope', sans-serif;
}

blockquote,body,div,h1,h2,h3,h4,h5,h6,ol,p,ul{
  margin:0;
  padding:0
}

img{
  width:100%;
  height:auto;
  display:block
}

a{
  text-decoration:none;
  color:inherit
}

ol,ul{
  list-style:none
}

.wrapper{
  max-width:450px;
  padding:0 18px;
  margin:0 auto
}

header{
  width: 100%;
  margin-bottom: 1rem;
  padding-bottom: 5px;
  color: #fff;
}

header .logo{
  width: 220px;
  padding: 4px 0;
  margin: 0 auto;
}

header h1{
  text-align: center;
  font-size: 28px;
  padding: 10px;
  font-weight: 300;
}

aside{
  font-size:10px;
  margin-bottom:1rem;
  color:#000;
  margin-top: 1rem;
  text-align: left;
}

.intro{
  position: fixed;
  width: 100%;
  z-index: 200;
  height: 100vh;
  background-color: var(--footerColor);
  transition: left 0.5s;
  left: 0;
}

.intro-wrapper{
  max-width: 450px;
  padding:2rem;
  margin: 0 auto;
  height: 100vh;
  display: flex;
  flex-direction: column;
  justify-content: center;
}

.intro.hide{
  left: -110vw;
}


.enter{
  align-self: center;
  padding: 10px 5px;
  text-align: center;
  opacity: 1;
  transition: opacity 0.5s;
  margin-top: 5rem;
  margin-bottom: 5rem;
  width: 100%;;
}

.enter button{
  width: 80%;
  border: 1px solid var(--borderColor);
    padding: 0.5rem 1rem;
    background: var(--btnColor);
    color: #fff;
    font-size: 14px;
    border-radius: 20px;  
}

.enter.hide{
  opacity: 0;
}

.enter span{
  display: block;
  color: #fff;
  font-size: 14px;
  padding: 0.5rem 1rem;
  letter-spacing: 0.8px;
}

.intro-logo{
  opacity: 1;
  transition: opacity 0.8s;
  margin: 0 auto;
  width:250px;
}


.intro-logo.hide{
  opacity: 0;
}

.filters-wrapper{
  width: calc(100% + 18px);
}

.filters{
  width: 100%;
}

.filters ul{
  display:-webkit-flex;
  display:flex;
  margin-bottom:.5rem;
  max-width:100%;
  overflow-x:scroll;
  -ms-scroll-snap-type:x mandatory;
  scroll-snap-type:x mandatory;
  -ms-overflow-style:none;
  scrollbar-width:none;
  width: fit-content;
}

.filters ul::-webkit-scrollbar{
  display:none
}

.filters ul li{
  border:1px solid #705D56;
  padding:6px 8px;
  font-size:12px;
  background-color:#6E4555;
  color:#fff;
  scroll-snap-align:start;
  border-radius: 10px 0;
  flex-shrink: 0;
  letter-spacing: 0.5px;
}

.filters ul li.active{
  background-color:#048BA8;
  border:1px solid #fff;
}

.filters ul li:not(:last-of-type){
  margin-right:.5rem
}


@media screen and (max-width:400px){
  .wrapper{
    padding: 0 10px;
  }
  .filters-wrapper{
    width: calc(100% + 10px);
  }
}