.CategoryTab{
    border:2px solid var(--borderColor);
    width: 98%;
    margin: 0 auto 1rem;
    height: 158px;
    overflow: hidden;
    position: relative;
    border-radius: 0 20px;
}

.CategoryTab:after{
    content: '';
    left: 0;
    bottom: 0;
    position: absolute;
    background: linear-gradient(0deg, rgba(0,0,0,1) 0%, rgba(0,0,0,0) 30%);
   /*background-color: rgba(0,0,0,0.1);*/
    width: 100%;
    height: 100%;
}


.CategoryLinkWrapper{
    width: 100%;
    height: 100%;;
}
.CategoryInnerWrapper{
    width: 100%;
    height: 100%;
    display: flex;
    align-items: flex-end;
}

.TabImg{
    width: 100%;
    height: 100%;;
    position: absolute;
    left:0;
    top: 0;
    
}

.TabImg img{
    object-fit: cover;
    object-position: center;
    width: inherit;
    height: inherit;
}

.CategoryTab h2{
    position: relative;
    z-index: 2;
    color: #fff;
    padding: 10px;
    font-weight: 400;
    font-size: 24px;
    text-align: center;
    letter-spacing: 1px;
    display: flex;
    align-items: flex-end;
    height: 100%;
    justify-content: center;
    text-shadow: 2px 2px 2px #141115; 
}


@media screen and (max-width: 400px) {
    .CategoryTab h2{
        padding: 5px;
        letter-spacing: 0.5px;
    }
}

