.FooterWrapper{
    max-width: 450px;
    margin: 0 auto;
}

.Address{
    display: block;
    text-align: center;
    align-items: center;
    position: relative;
    width: 100%;
}

.Address address{
    font-style: normal;
    font-weight: 100;
    font-size: 16px;
}

.Address a{
    display: block;
    font-size: 18px;
    padding: 5px;
    border-radius: 5px;

}

.Footer address span{
    display: block;
}



.Footer h3{
    font-weight: 100;
}

.SocialWrap{
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
    padding: 0 12px;
}

.SocialWrap h3{
    width: 100%;
    position: relative;
    padding-bottom: 3px;
    padding-top: 10px;
    font-size: 22px;
    margin-bottom: 5px;
    border-bottom:1px solid;
}

.SocialWrap h4{
    font-weight: 100;
    font-size : 18px;  
    margin-bottom: 5px; 
}

.FooterLogo{
    width: 50%;
    padding: 20px;
}

.Social{
    display: flex;
    justify-content: center;
    width: 100%;
    margin: 0 auto;
    align-items: flex-start;
    flex-wrap: wrap;
}

.Social li{
    padding: 0.5rem;
}

.Social li a{
    /*transform: scale(0.5);*/
    display: block;
}

.Social li a i{
    margin-right: 5px;
}

.Phones{
    padding: 10px 0;;
    width: 100%;
    background-color: var(--btnColor);
    color: #fff;
}

.Phones h4{
    font-size: 22px;
}

.PhonesList{
    display: flex;
    width: 90%;
    margin: 0 auto;
    justify-content: space-around;
    margin-top: 10px;
}


.PhonesList > div{
    display: flex;
    flex-direction: column;
    font-size: 10px;
    line-height: 12px;
}

.PhonesList > div span{
    display: block;
    font-size: 14px;
    margin-bottom: 5px;
}
.Phones > div a{
    font-size: 20px;
    line-height: 20px;
    letter-spacing: 0.85px;
}

.Developer{
    font-size: 12px;
    padding: 0.25rem 0 0.5rem;
    position: relative;
    font-weight: 100;
    display: flex;
    justify-content: center;
    align-items: flex-end;
}

.Developer h6{
    margin-left: 1ch;
}

.Developer:before{
    content: "";
    width: 20%;
    height: 1px;
    background-color: #fff;
    left: 40%;
    top:0;
    position: absolute;
}

.Developer h6{
    font-size: 16px;
    font-weight: 100;

}


@media screen and (max-width:405px){
    .Social li{
        font-size: 14px;
    }
}

@media screen and (max-width:380px){
    .SocialWrap{
        padding: 0 6px;
    }
}