.ProductSubList:not(:last-of-type){
    margin-bottom: 1rem;

}
.ProductSubList h4{
    /* margin-bottom: 8px; */
    font-size: 20px;  
    text-align: left;
    padding: 2px 24px 2px 5px;
    /*color: #a53860;
    border:1px solid #A53860;
    border-left: 1.5rem solid #A53860;*/
    margin-bottom: 1rem;
    background-color: var(--secHeaderColor);
    color: #fff;
    letter-spacing: 0.5px;
    font-weight: 300;
    position: relative;
    text-align: right;
}

.ProductSubList h4:after{
    content: '';
    width: 100%;
    height: 10px;
    position: absolute;
    left: 0;
    top: -5px;
    background-color: var(--secHeaderColor);
    z-index: -1;
    transform: skewY(-1deg);
}
.ProductSubList h4:before{
    content: '';
    width: 100%;
    height: 10px;
    position: absolute;
    left: 0;
    bottom: -5px;
    background-color: var(--secHeaderColor);
    z-index: -1;
    transform: skewY(1deg);
}


.ProductSubList:nth-of-type(odd) h4{
    text-align: left;
    padding: 2px 5px 2px 24px;
}

.ProductSubList:nth-of-type(odd) h4:after{
    transform: skewY(1deg);
}
.ProductSubList:nth-of-type(odd) h4:before{
    transform: skewY(-1deg);
}
