.MenuGrid{
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
  }
  

  .Menu > h2{
    color: #fff;
    font-weight: 300;
    margin-bottom: 1.25rem;
    padding: 5px 10px;
    background-color: var(--headerColor);
    position: relative;
  }

  .Menu > h2:after{
    content: '';
    top: -6px;
    left: 0;
    position: absolute;
    background-color: var(--headerColor);
    z-index: -1;
    width: 100%;
    height: 15px;
    transform: skewY(-1deg);
}
  .Menu > h2:before{
    content: '';
    bottom: -6px;
    left: 0;
    position: absolute;
    background-color: var(--headerColor);
    z-index: -1;
    width: 100%;
    height: 15px;
    transform: skewY(1deg);
}

  .Manager{
    background-color: #808275;
    color: #fff;
    margin-bottom: 10px;
  }

  .Manager h4{
    font-weight: 300;
    text-align: center;
    padding: 5px;
  }

  .Manager h4 span{
    display: block;
  }

  .Alert{
    background-color: #BC471B;
    color: #fff;
    margin-bottom: 10px; 
    padding: 6px 12px; 
  }

  .Alert h5{
    margin:0 auto;
    border: 1px solid #fff;
    font-weight: 300;
    padding: 6px;
  }